@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


/* HEADER */

.App{
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    font-size: 16px;
    width: 1280px;
    margin: 50px auto; 
}




header{
    font-weight: 600;
    font-size: 30px;
    position: relative;
    

    .Nothing{
        text-align: center;
        line-height: 100px;
        

        p{
            font-weight: 600;
        }
    }



        .shop__sum{
            padding: 0px 0px 20px 30px;
            font-weight: 700;
            font-size: 25px;


            b{
                font-weight: 500;
                color:#5fa35f;
            }
        }


    
    .ShopItem{

        display: flex;
        padding: 15px;
        border: 1px solid #e4e4e4;
        border-radius: 40px;
        background-color: #e4e4e4;
        margin: 20px;
        align-items: center;
        justify-content: space-between;


        img{
            width: 100px;
            height: 100px;
            border-radius: 30px;
        }

        .ShopItem__info{
            display: flex;
            flex-direction: column;
            gap: 5px;
            text-align: center;


            h3{
                font-size: 20px;
                width: 150px;
            }

            b{
                font-size: 16px;
                color: #797979;
            }

        }


        
        .trash__btn{
            border-radius: 15px;
            padding: 2px;
            color: #dc3d3d;
            font-weight: 600;
            width: 85px;
            cursor: pointer;
        }

    }

    .menu{
        display: flex;
        justify-content: space-between;
        align-items: center;


        .shop-card{
            position: absolute;
            top: 30px;
            right: 0px;
            width: 450px;
            min-height: 100px;
            background-color: #fafafa;
            -webkit-box-shadow: 2px 1px 18px 1px rgba(0,0,0,0.75);
            -moz-box-shadow: 2px 1px 18px 1px rgba(0,0,0,0.75);
            box-shadow: 2px 1px 18px 1px rgba(0,0,0,0.75);
            z-index: 1000;
            margin-top: 10px;
        }



        
        


        ul{
            font-weight: 300;
            font-size: 20px;
            list-style: none;
            display: flex;
            gap: 25px;
            align-items: center;


            li{
                cursor: pointer;
            
                .shop-card-btn{
                    transform: translateY(2px);
                    transition: 500ms;
                }

                


                .shop-card-btn:hover,
                .active{
                    transform: scale(1.05);
                    color: #dc3d3d
                }

            }
        }
    
    }


    .presentation{
        margin: 50px 0;
        background: url('./../../public/image/bg-image.jpeg');
        width: 100%;
        height: 500px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        background-blend-mode: multiply;
        background-color: #bcbcbc;
        position: relative;
    }

    .presentation::after{
        content: "Лучшие товары для вашего дома";
        font-size: 40px;
        position: absolute;
        top: 100px;
        left: 50px;
        width: 300px;
        color: white;
    }



    .presentation::before{
        content: "по низким ценам";
        font-size: 15px;
        position: absolute;
        top: 250px;
        left: 50px;
        width: 300px;
        color: white;
        font-weight: 300;
    }

}



/* Category */


.categories{
    display: flex;
    gap: 35px;

    div{
        background-color: #e4e3e3;
        border-radius: 50px;
        padding: 10px 20px;
        margin-bottom: 50px;
        cursor: pointer;
        transition: 500ms ease;
    }

    div:hover{
        transform: scale(1.08);
    }
}


/* POSTITEM */


.full-item{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #252525;
    z-index: 9999;
    overflow: hidden;


    .full-item-div{
        position: relative;
        width: 600px;
        margin: 10% auto;
        padding: 40px 30px;
        background: #fff;
        border-radius: 10px;
        margin-top: 100px;
        margin-bottom: auto;
    
    }


    img{
        width: 100%;
        border-radius: 25px 25px 0px 0px;
        transition: 200ms;
        cursor: pointer;
        margin-bottom: 15px;
    }

    img:hover{
        transform: scale(1.05);
    }

h2, p, b{
    margin: 10px 20px;
    color: #333;
}

b{
    color: #5fa35f;
}


.add-to-card{
    position: absolute;        
    bottom: 20px;
    right: 15px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    font-weight: 600;
    transition: 500ms ease;
}

.add-to-card:hover{
    transform: scale(1.2) translateY(-5px);
}

    .cross{
        position: absolute;
        right: 2px;
        top: 1.5px;
        font-size: 35px;
        font-weight: 600;
        cursor: pointer;
    }

}


.main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between; 



    .item{
        width: 30%;
        margin-bottom: 50px;
        background-color: #d3d2d2;
        overflow: hidden;
        position: relative;
        padding-bottom: 20px;
        border-radius: 25px;


        img{
            width: 100%;
            border-radius: 25px 25px 0px 0px;
            transition: 200ms;
            cursor: pointer;
            margin-bottom: 15px;
        }

        img:hover{
            transform: scale(1.05);
        }

    h2, p, b{
        margin: 10px 20px;
        color: #333;
    }

    b{
        color: #5fa35f;
    }


    .add-to-card{
        position: absolute;        
        bottom: 20px;
        right: 15px;
        background: #ca5252;
        width: 35px;
        height: 35px;
        text-align: center;
        line-height: 35px;
        border-radius: 50%;
        color: white;
        cursor: pointer;
        font-weight: 600;
        transition: 500ms ease;
    }

    .add-to-card:hover{
        transform: scale(1.2) translateY(-5px);
    }


    }
    
}










/* Footer */

footer{
    margin-top: 100px;
    text-align: center;
}